import React, { useState, useEffect, useRef } from "react";
import { Form } from "react-bootstrap";
import styles from "../../UI/css/Slider.module.css";
import ElemDeleteControl from "../BasicFormControls/ElemDeleteControlWrapper";
import styles2 from "../css/COLUMN.module.css";
import Slider from "@material-ui/core/Slider";
import Grid from "@material-ui/core/Grid";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import Tooltip from "@material-ui/core/Tooltip";
import { sendRequestToUpdateValue } from "../../components/IFormDesignerLayout/RequestLogic";
import EditableLabel from "../../ReusableComponents/EditableLabel/EditableLabel";
import QuickTools from "../../ReusableComponents/QuickTools/QuickTools";
import { triggerEventCalls } from "../../ReusableComponents/Events/EventExecutionLogic";
import { getSuggestedId } from "../../components/IFormDesignerLayout/Logic/UserIdLogic";
import { useTranslation } from "react-i18next";
import {
  GetState,
  GetStateFromData,
} from "../../components/CustomHooks/CustomHooks";
import CustomizedAlert from "../../ReusableComponents/Alert/CustomizedAlert";
import MandatoryMsg from "../../ReusableComponents/ErrorMessageComponent/MandatoryMsg";
import {
  handleDisplayProperty,
  setMandatoryControls,
  updateValue,
} from "../../components/IFormDesignerLayout/IFormDesignerLogic";
import { cloneDeep } from "lodash";
import { ThemeProvider } from "@material-ui/styles";
import { createTheme } from "@material-ui/core/styles";
const useStyles = makeStyles({
  sliderRoot: {
    "& .MuiGrid-root": {
      width: "100% !important",
      margin: "0px!important",
    },
    "& .MuiSlider-track": {
      backgroundColor: (styles) => styles.ActiveSliderBar,
      height: "5px",
    },
    "& .MuiSlider-rail": {
      color: (styles) => styles.InActiveSliderBar,
      height: "5px",
    },
    "& .MuiSlider-thumb": {
      height: "13px",
      width: "13px",
      backgroundColor: "white",
      border: (style) =>
        style.showMandatoryMsg ? "2px solid red" : "2px solid blue",
      borderColor: (styles) =>
        styles.showMandatoryMsg ? "2px solid red" : styles.ActiveSliderBar,
    },
    "& .MuiSlider-mark MuiSlider-markActive": {
      backgroundColor: (styles) => styles.ActiveSliderBar,
    },
    "& .MuiSlider-markLabel": {
      transform:
        window?.appformBuilder_configs?.selectedLanguage === "ar" ||
        window?.appformBuilder_configs?.selectedLanguage === "ar-SA"
          ? "translate(60%, 0)"
          : "translate(-60%, 0)",
    },
    "& .MuiSlider-mark": {
      backgroundColor: "#ffffff",
    },
    "& .MuiSlider-markLabel.MuiSlider-markLabelActive": {
    },

    "&  .MuiSlider-valueLabel": {
      backgroundColor: "inherit",
      "& > span": {
        backgroundColor: "#243EDO",
        borderRadius: "2%",
        transition: "transform 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms",
        position: "absolute",
        transform: "rotate(0deg)",
        display: "block",
        width: "auto",
        height: "auto",
        padding: "0.25rem 0.75rem",
      },
    },
    "& .MuiInput-underline:before": {
      height: "6px",
      borderBottom: "1px solid black !important",
    },
    "& .MuiInput-underline:after": {
      width: "35px",
      height: "6px",
      borderBottom: "none",
    },
    "& .MuiInput-underline:hover:not(.Mui-disabled):before": {
      borderBottom: "1px solid rgba(0, 0, 0, 0.42)",
    },
    "& .MuiInputBase-input": {
      width: "40px",
      height: "10px",
      fontSize: "12px",
      marginBottom: "1.5px",
      border: "none !important",
      "&:focus": {
        outline: "none !important",
        boxShadow: "none !important",
        border: "none !important",
        WebkitBoxShadow: "none !important",
        height: "22px",
        width: "28px",
        padding: "0 !important",
        marginBottom: "1.5px",
      },
    },
    "& .Mui-focused": {
      border: "none !important",
    },
    input: {
      border: "none !important"
    },
  },
  sliderInput2: {
    fontSize: (style) => `${style.fontSize} !important`,
    height: (style) => `calc(${style.fontSize} + 5pt) !important`,
    width: (style) =>
      parseInt(style.fontSize?.replace("px")) <= 13 ? "72px" : "85px",
    paddingBottom: "0",
    paddingLeft: "15px !important",
  },
});

function SLIDER(props) {
  const contexts = GetStateFromData(props.containerId);
  const { tempCanWidth, controlSelected, mandatoryControls } = GetState(
    "tempCanWidth",
    "controlSelected",
    "mandatoryControls"
  );
  const reduxContexts = contexts;

  const translationStyles1 =
    window?.appformBuilder_configs?.selectedLanguage === "ar" ||
    window?.appformBuilder_configs?.selectedLanguage === "ar-SA";

  const theme = createTheme({
    direction: translationStyles1 ? "rtl" : "ltr",
  });

  const { t } = useTranslation();

  let userDefinedId = props.data["style"] && props.data["style"]["CustomId"];
  const {
    IdPrecedence,
    modeReadOrWrite,
    openQuickToolFor,
    deleteControl,
    device,
    formState2,
    themeOpen,
    themeResetDone,
    themeResetValDone,
  } = contexts.contexts;

  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification",
  });

  const [showMandatoryMsg, setShowMandatoryMsg] = useState(false);

  const isSubformInPage = props?.isSubformInPage || false;

  if (props.previewmode && modeReadOrWrite === "r") {
    props.data.style.Enable = "false";
  }
  useEffect(() => {
    if (mandatoryControls.includes(props.data?.id)) {
      setShowMandatoryMsg(true);
    } else {
      setShowMandatoryMsg(false);
    }
  }, [mandatoryControls]);

  function showAlertConfirmNotifications(dataTobeSet) {
    setAlertData(dataTobeSet);
  }

  const actualControlId = props.data.id;
  let mappingId = props.data["style"] && props.data["style"]["MappingId"];
  let userDefinedControlId = getSuggestedId(
    actualControlId,
    mappingId,
    userDefinedId,
    IdPrecedence
  );

  useEffect(() => {
    if (
      userDefinedControlId &&
      document.getElementById(`${userDefinedControlId}_input`)
    )
      document.getElementById(`${userDefinedControlId}_input`).style.border =
        "none";
  }, [userDefinedControlId]);

  const themeData = formState2.theme;

  const controlThemeData = themeData?.["InputStyle"];

  const defaultStyle = {
    color: "#000000",
    fontFamily: "Open Sans",
    fontSize: "14px",
    fontWeight: "400",
    fontStyle: "bold",
    paddingBottom: "0px",
    paddingTop: "0px",
    paddingLeft: "2px",
    paddingRight: translationStyles1 ? "0px" : "14px",
    rupee: "",
    ActiveSliderBar: "",
    InActiveSliderBar: "",
    float: "Left",
  };

  const data = props.data;

  const propsDataStyle = props?.data?.style;

  const [state, setState] = useState(false);

  const sliderThemeStyle = useRef(defaultStyle);
  const defaultBuffer2 = cloneDeep(defaultStyle);
  const [cloneThemeStyle, setCloneThemeStyle] = useState();
  const [rerenderState, setReRenderState] = useState(false);

  const setSliderStyle = (newStyle) => {
    const prevState = JSON.stringify(sliderThemeStyle.current);
    sliderThemeStyle.current = { ...sliderThemeStyle.current, ...newStyle };
    if (!props.previewmode) {
      if (prevState !== JSON.stringify(sliderThemeStyle.current)) {
        setReRenderState(!rerenderState);
      }
    }
  };

  useEffect(() => {
    setState(!state);
  }, []);

  useEffect(() => {
    if (themeOpen === true || themeOpen === "true") {
      setCloneThemeStyle(cloneDeep(sliderThemeStyle.current));
    }
  }, [themeOpen]);

  useEffect(() => {
    if (themeResetDone === true || themeResetDone === "true") {
      Object.assign(sliderThemeStyle.current, cloneThemeStyle);
    }
  }, [themeResetValDone]);

  useEffect(() => {
    if (
      themeData?.["BasicTheme"]?.["FontFamily"] &&
      (!controlThemeData?.["LabelFontFamily"] ||
        controlThemeData["LabelFontFamily"] === "Select") &&
      (!propsDataStyle?.["FontFamily"] ||
        propsDataStyle?.["FontFamily"] === "Select")
    ) {
      setSliderStyle({ fontFamily: themeData["BasicTheme"]["FontFamily"] });
    }
    if (
      (themeData?.["BasicTheme"]?.["FontFamily"] === "" ||
        themeData?.["BasicTheme"]?.["FontFamily"] === "Select") &&
      (!controlThemeData["LabelFontFamily"] ||
        controlThemeData["LabelFontFamily"] === "Select") &&
      (!propsDataStyle["FontFamily"] ||
        propsDataStyle["FontFamily"] === "Select")
    ) {
      setSliderStyle({ fontFamily: defaultBuffer2.fontFamily });
    }
    if (
      themeData?.["BasicTheme"]?.["FontSize"] &&
      !controlThemeData?.["LabelFontSize"] &&
      (!propsDataStyle?.["FontSize"] ||
        propsDataStyle?.["FontSize"] === "Select")
    ) {
      setSliderStyle({ fontSize: themeData["BasicTheme"]["FontSize"] });
    }
    if (
      themeData?.["BasicTheme"]?.["ActiveTextColor"] &&
      !controlThemeData?.["LabelFontColor"] &&
      !propsDataStyle["FontColorSlider"]
    ) {
      setSliderStyle({ color: themeData?.["BasicTheme"]?.["ActiveTextColor"] });
    }
  }, [themeData?.["BasicTheme"]]);

  useEffect(() => {
    if (
      themeData?.["SliderTheme"]?.["SlideBarHeader"] &&
      !propsDataStyle?.["ActiveSliderBar"]
    )
      setSliderStyle({
        ActiveSliderBar: themeData?.["SliderTheme"]?.["SlideBarHeader"],
      });
  }, [themeData?.["SliderTheme"]?.["SlideBarHeader"]]);

  useEffect(() => {
    if (
      controlThemeData &&
      controlThemeData["LabelFontFamily"] &&
      (!propsDataStyle?.["FontFamily"] ||
        propsDataStyle?.["FontFamily"] === "Select")
    ) {
      setSliderStyle({ fontFamily: controlThemeData["LabelFontFamily"] });
    }
    if (
      controlThemeData &&
      (controlThemeData["LabelFontFamily"] === "" ||
        controlThemeData["LabelFontFamily"] === "Select") &&
      (!propsDataStyle?.["FontFamily"] ||
        propsDataStyle?.["FontFamily"] === "Select")
    )
      setSliderStyle({
        fontFamily:
          themeData?.["BasicTheme"]?.["FontFamily"] &&
          themeData?.["BasicTheme"]?.["FontFamily"] !== "Select"
            ? themeData?.["BasicTheme"]?.["FontFamily"]
            : defaultBuffer2.fontFamily,
      });
    if (
      controlThemeData &&
      controlThemeData["LabelFontSize"] &&
      (!propsDataStyle?.["FontSize"] ||
        propsDataStyle?.["FontSize"] === "Select")
    ) {
      setSliderStyle({ fontSize: controlThemeData["LabelFontSize"] });
    }
    if (
      controlThemeData &&
      (controlThemeData["LabelFontSize"] === "" ||
        controlThemeData["LabelFontSize"] === "Select") &&
      (!propsDataStyle?.["FontSize"] ||
        propsDataStyle?.["FontSize"] === "Select")
    ) {
      setSliderStyle({
        fontSize:
          themeData?.["BasicTheme"]?.["FontSize"] &&
          themeData?.["BasicTheme"]?.["FontFamily"] !== "Select"
            ? themeData?.["BasicTheme"]?.["FontSize"]
            : defaultBuffer2.fontSize,
      });
    }
    if (
      controlThemeData &&
      controlThemeData["LabelFontWeight"] &&
      (!propsDataStyle?.["FontWeight"] ||
        propsDataStyle?.["FontWeight"] === "Select")
    ) {
      if (controlThemeData["LabelFontWeight"].toLowerCase() === "italic") {
        setSliderStyle({ fontStyle: "italic" });
        setSliderStyle({ fontWeight: 300 });
      } else if (controlThemeData["LabelFontWeight"].toLowerCase() === "bold") {
        setSliderStyle({ fontStyle: "normal" });
        setSliderStyle({ fontWeight: "bold" });
      } else if (
        controlThemeData["LabelFontWeight"].toLowerCase() === "normal"
      ) {
        setSliderStyle({ fontStyle: "normal" });
        setSliderStyle({ fontWeight: 300 });
      } else if (
        controlThemeData["LabelFontWeight"].toLowerCase() === "bold italic"
      ) {
        setSliderStyle({ fontStyle: "italic" });
        setSliderStyle({ fontWeight: "bold" });
      }
    }
    if (
      controlThemeData &&
      (controlThemeData["LabelFontWeight"] === "" ||
        controlThemeData["LabelFontWeight"] === "Select") &&
      (!propsDataStyle?.["FontWeight"] ||
        propsDataStyle?.["FontWeight"] === "Select")
    ) {
      setSliderStyle({ fontStyle: defaultBuffer2.fontStyle });
      setSliderStyle({ fontWeight: defaultBuffer2.fontWeight });
    }
    if (
      controlThemeData &&
      controlThemeData["LabelFontColor"] &&
      !propsDataStyle?.["FontColorSlider"]
    ) {
      setSliderStyle({ color: controlThemeData["LabelFontColor"] });
    }
  }, [controlThemeData]);

  useEffect(() => {
    if (propsDataStyle?.["DefaultUnit"]) {
      if (propsDataStyle["DefaultUnit"] === "Dollars")
        setSliderStyle({ rupee: "$" });
      else if (props.data.style["DefaultUnit"] === "Indian")
        setSliderStyle({ rupee: "" });
      else setSliderStyle({ rupee: "" });
    }
    if (propsDataStyle?.["FontColorSlider"])
      setSliderStyle({ color: propsDataStyle["FontColorSlider"] });
    if (propsDataStyle?.["FontFamily"])
      setSliderStyle({ fontFamily: propsDataStyle["FontFamily"] });
    if (
      propsDataStyle?.["FontFamily"] === "" ||
      propsDataStyle?.["FontFamily"] === "Select"
    )
      setSliderStyle({
        fontFamily:
          controlThemeData?.["LabelFontFamily"] &&
          controlThemeData?.["LabelFontFamily"] !== "Select"
            ? controlThemeData?.["LabelFontFamily"]
            : themeData?.["BasicTheme"]?.["FontFamily"] &&
              themeData?.["BasicTheme"]?.["FontFamily"] !== "Select"
            ? themeData?.["BasicTheme"]?.["FontFamily"]
            : defaultBuffer2.fontFamily,
      });
    if (propsDataStyle?.["FontWeight"])
      setSliderStyle({ fontWeight: propsDataStyle["FontWeight"] });
    if (propsDataStyle?.["FontStyle"])
      setSliderStyle({ fontStyle: propsDataStyle["FontStyle"] });
    if (
      propsDataStyle?.["FontWeight"] === "" ||
      propsDataStyle?.["FontWeight"] === "Select"
    ) {
      if (
        controlThemeData?.["LabelFontWeight"] === "" ||
        controlThemeData?.["LabelFontWeight"] === "Select"
      ) {
        setSliderStyle({ fontStyle: defaultBuffer2.fontStyle });
        setSliderStyle({ fontWeight: defaultBuffer2.fontWeight });
      } else {
        if (controlThemeData["LabelFontWeight"].toLowerCase() === "italic") {
          setSliderStyle({ fontStyle: "italic" });
          setSliderStyle({ fontWeight: 300 });
        } else if (
          controlThemeData["LabelFontWeight"].toLowerCase() === "bold"
        ) {
          setSliderStyle({ fontStyle: "normal" });
          setSliderStyle({ fontWeight: "bold" });
        } else if (
          controlThemeData["LabelFontWeight"].toLowerCase() === "normal"
        ) {
          setSliderStyle({ fontStyle: "normal" });
          setSliderStyle({ fontWeight: 300 });
        } else if (
          controlThemeData["LabelFontWeight"].toLowerCase() === "bold italic"
        ) {
          setSliderStyle({ fontStyle: "italic" });
          setSliderStyle({ fontWeight: "bold" });
        }
      }
    }

    if (propsDataStyle?.["FontSize"])
      setSliderStyle({ fontSize: propsDataStyle["FontSize"] });
    if (
      propsDataStyle?.["FontSize"] === "" ||
      propsDataStyle?.["FontSize"] === "Select"
    ) {
      setSliderStyle({
        fontSize:
          controlThemeData?.["LabelFontSize"] &&
          controlThemeData?.["LabelFontSize"] !== "Select"
            ? controlThemeData?.["LabelFontSize"]
            : themeData?.["BasicTheme"]?.["FontSize"] &&
              themeData?.["BasicTheme"]?.["FontSize"] !== "Select"
            ? themeData?.["BasicTheme"]?.["FontSize"]
            : defaultBuffer2.fontSize,
      });
    }
    if (propsDataStyle?.["LabelAlignmentSlider"]) {
      setSliderStyle({ float: propsDataStyle["LabelAlignmentSlider"] });
    }
    if (
      propsDataStyle?.["LabelAlignmentSlider"] === "" ||
      propsDataStyle?.["LabelAlignmentSlider"] === "Select"
    )
      setSliderStyle({ float: "Left" });
    if (propsDataStyle?.["SpacingLeft"]) {
      if (propsDataStyle?.["SpacingLeft"] === "0pt")
        setSliderStyle({ paddingLeft: "2px" });
      else setSliderStyle({ paddingLeft: propsDataStyle["SpacingLeft"] });
    }
    if (propsDataStyle?.["SpacingRight"]) {
      if (propsDataStyle?.["SpacingRight"] === "0pt")
        setSliderStyle({ paddingRight: "14px" });
      else setSliderStyle({ paddingRight: propsDataStyle["SpacingRight"] });
    }
    if (propsDataStyle?.["SpacingTop"]) {
      if (propsDataStyle?.["SpacingTop"] === "0pt")
        setSliderStyle({ paddingTop: "0px" });
      else setSliderStyle({ paddingTop: propsDataStyle["SpacingTop"] });
    }
    if (propsDataStyle?.["SpacingBottom"]) {
      if (propsDataStyle?.["SpacingBottom"] === "0pt")
        setSliderStyle({ paddingBottom: "0px" });
      else setSliderStyle({ paddingBottom: propsDataStyle["SpacingBottom"] });
    }
    if (propsDataStyle?.["ActiveSliderBar"])
      setSliderStyle({ ActiveSliderBar: propsDataStyle["ActiveSliderBar"] });
    if (propsDataStyle?.["InActiveSliderBar"])
      setSliderStyle({
        InActiveSliderBar: propsDataStyle["InActiveSliderBar"],
      });
  }, [JSON.stringify(propsDataStyle)]);

  const [value, setValue] = useState(0);

  const valueLength = value?.toString().length;

  var style = {
    ...sliderThemeStyle.current,
    ...style,
    showMandatoryMsg,
    valueLength,
  };

  const classes = useStyles(style);

  const conditionalQuickToolOpening =
    props.id === openQuickToolFor ? true : false;

  const [open, setOpen] = useState(conditionalQuickToolOpening);

  var valueOfSlider = props.data.style && parseInt(props.data.style["value"]);
  if (
    valueOfSlider !== undefined &&
    valueOfSlider !== null &&
    valueOfSlider != ""
  ) {
  } else {
    valueOfSlider = 0;
  }

  const [defaultLabel, setDefaultLabel] = useState(
    props?.data?.style?.["Label"] || props?.data?.style?.["Label"] === ""
      ? props?.data?.style?.["Label"]
      : props?.data?.id
  );

  useEffect(() => {
    if (
      props.data.style &&
      (props?.data?.style?.["Label"] || props?.data?.style?.["Label"] === "")
    ) {
      setDefaultLabel(props.data.style["Label"]);
    }
  }, [props.data.style["Label"]]);

  const [editLabel, setEditLabel] = useState(false);
  const bringEditableLabel = (event) => {
    if (isSubformInPage) {
      setEditLabel(false);
    } else setEditLabel(true);
  };
  const elementRefrence = useRef(null);

  const [mobileDisplay, setMobileDisplay] = useState(true);
  const [tabDisplay, setTabDisplay] = useState(true);

  const startMobile = useRef();
  const endMobile = useRef();
  const startTab = useRef();
  const endTab = useRef();

  useEffect(() => {
    if (formState2 && formState2.style) {
      startMobile.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]
      );
      endMobile.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]
      );
      startTab.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]
      );
      endTab.current = parseInt(
        formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]
      );
    }
  }, [
    formState2.style?.["Breakpoint"],
    formState2.style?.["Breakpoint"]?.["Mobile"],
    formState2.style?.["Breakpoint"]?.["Tab"],
  ]);

  useEffect(() => {
    if (props?.previewmode)
      handleDisplayProperty(
        props,
        device,
        setMobileDisplay,
        setTabDisplay,
        startMobile.current,
        endMobile.current,
        startTab.current,
        endTab.current
      );
  }, [
    props?.data?.style?.["MobileVisible"],
    props?.data?.style?.["TabsVisible"],
    device,
    endMobile,
    endTab,
    startMobile,
    startTab,
    tempCanWidth,
  ]);

  //Sets the label value into the formState
  const setLabelHandler = (recievedLabel) => {
    try {
      if (
        props.data &&
        props?.data?.style &&
        (props?.data?.style?.Label || props?.data?.id)
      ) {
        setDefaultLabel((prevDefaultLabel) => {
          if (prevDefaultLabel === recievedLabel) {
            return prevDefaultLabel;
          } else {
            props.data.style["Label"] = recievedLabel;
            return recievedLabel;
          }
        });
        setEditLabel(false);
      }
    } catch (err) {
      console.error(err);
    }
  };

  useEffect(() => {
    if (
      props.data &&
      props.data.value &&
      props.data.value != null &&
      props.data.value != undefined &&
      props.data.value != ""
    ) {
      setValue(parseInt(props.data.value));
    } else {
      setValue(
        props.data.style && parseInt(props.data.style["DefaultValueSlider"])
      );
    }
  }, [props.data.value]);
  useEffect(() => {
    if (!props.previewmode)
      setValue(
        props.data.style && parseInt(props.data.style["DefaultValueSlider"])
      );
  }, [props.data.style && props.data.style["DefaultValueSlider"]]);

  const handleSliderChange = (event, newValue) => {
    setValue(newValue);
    removeIdFromMandatoryControl(newValue);
  };

  const handleInputChange = (event) => {
    setValue(event.target.value === "" ? "" : Number(event.target.value));
    removeIdFromMandatoryControl(event.target.value);
  };

  const handleBlur = (event) => {
    if (value < minimumValueOfSlider || value > maximumValueOfSlider) {
      setValue("");
      setAlertData({
        open: true,
        showConfirm: "showNotification",
        alertType: "error",
        alertContentMsg: `${t("Invalid_Input")}`,
      });
    } else if (
      value >= minimumValueOfSlider &&
      value <= maximumValueOfSlider &&
      props.previewmode
    ) {
      var jsonToSend = {
        attrName: props.data.dataclass || userDefinedId || props.data.id,
        attrValue: value.toString(),
        attrType: "slider",
      };
      sendRequestToUpdateValue(jsonToSend, props?.containerId).then(
        (response) => {
          if (response?.status === "-2") {
            setAlertData({
              open: true,
              showConfirm: "showNotification",
              alertType: "error",
              alertContentMsg: `${t("Invalid_Input")}`,
              autoHideDuration: 6000,
            });
          } else {
            props.data.value = value;
            updateValue(props.data.id, value, props.containerId);
            var e = {
              type: "change",
            };
            if (
              props.data &&
              props.data.event &&
              props.data.event.Events &&
              props.data.event.Events.Change
            )
              triggerEventCalls(
                e,
                { id: props?.data?.id },
                props.data.event,
                "slider",
                showAlertConfirmNotifications,
                reduxContexts
              );
            removeIdFromMandatoryControl(value);
          }
        }
      );
    }
  };

  function removeIdFromMandatoryControl(value) {
    if (mandatoryControls.includes(props.data.id)) {
      if (value) {
        const newMandatoryList = mandatoryControls.filter(
          (item) => item !== props.data.id
        );
        setMandatoryControls([...newMandatoryList]);
      }
    }
  }

  var minimumValueOfSlider =
    props.data.style && parseInt(props.data.style["MinimumValue"]);
  if (
    minimumValueOfSlider !== undefined &&
    minimumValueOfSlider !== null &&
    minimumValueOfSlider != ""
  ) {
  } else {
    minimumValueOfSlider = 0;
  }

  var maximumValueOfSlider =
    props.data.style && parseInt(props.data.style["MaximumValue"]);
  if (
    maximumValueOfSlider !== undefined &&
    maximumValueOfSlider !== null &&
    maximumValueOfSlider != ""
  ) {
  } else {
    maximumValueOfSlider = 0;
  }

  var defaultValueOfSlider =
    props.data.style && props.data.style["DefaultValueSlider"];
  if (
    defaultValueOfSlider !== undefined &&
    defaultValueOfSlider !== null &&
    defaultValueOfSlider != ""
  ) {
  } else {
    defaultValueOfSlider = 0;
  }

  var breakpoint =
    props.data.style && parseInt(props.data.style["SetBreakpoint"]);
  if (breakpoint !== undefined && breakpoint !== null && breakpoint != "") {
    breakpoint = Number(props.data.style["SetBreakpoint"]);
  } else {
    breakpoint = 1;
  }

  const inputStyle = {
    color: sliderThemeStyle.current.color,
    fontSize: sliderThemeStyle.current.fontSize,
    fontFamily: sliderThemeStyle.current.fontFamily,
    fontWeight: sliderThemeStyle.current.fontWeight,
    float: sliderThemeStyle.current.float,
    marginTop: "0px",
    lineHeight: sliderThemeStyle.current.fontSize,
    fontStyle: sliderThemeStyle.current.fontStyle,
    maxWidth: "75%",
    "word-break": "break-word",
    "white-space": "break-spaces",
  };

  var inputStyleInputPart = {
  };

  const marks = [
    {
      value: minimumValueOfSlider,
      label: minimumValueOfSlider + "",
    },
    {
      value: maximumValueOfSlider,
      label: maximumValueOfSlider + "",
    },
  ];

  var mandatoryElem = null;
  if (
    props.data &&
    props.data.style &&
    props.data.style["Mandatory"] !== undefined &&
    props.data.style["Mandatory"] !== null &&
    props.data.style["Mandatory"] === "true"
  ) {
    mandatoryElem = (
      <>
        <span
          style={{ color: "#D53D3D", marginLeft: "0px", alignSelf: "center" }}
        >
          {" "}
          *{" "}
        </span>
      </>
    );
  }

  var inputStylePadding = {
    display: "block",
    paddingLeft: sliderThemeStyle.current.paddingLeft,
    paddingRight: sliderThemeStyle.current.paddingRight,
    paddingTop: sliderThemeStyle.current.paddingTop,
    paddingBottom: sliderThemeStyle.current.paddingBottom,
  };

  if (props.previewmode) {
    return (
      <>
        {props.data &&
        props.data.style &&
        props.data.style["Visible"] === "true" ? (
          <>
            <Form.Group
              id={userDefinedControlId + "_group"}
              key={data.id}
              control_id={userDefinedControlId}
              frameindex={data.frameIndex}
              elementindex={data.elementIndex}
              style={
                mobileDisplay === false || tabDisplay === false
                  ? { display: "none" }
                  : inputStylePadding
              }
              className={" spacingThemeControls "}
            >
              <div
                style={{
                  display: "flex",
                  alignItems: "end",
                  gap: "5%",
                  marginRight: "14%",
                  justifyContent:
                    sliderThemeStyle?.current?.float === "Left"
                      ? "flex-start"
                      : "flex-end",
                }}
              >
                <Form.Label
                  id={userDefinedControlId + "_label"}
                  control_id={userDefinedControlId}
                  className={styles2.formStyle + "controlFontSize"}
                  frameindex={props.data.frameIndex}
                  style={inputStyle}
                  elementindex={props.data.elementIndex}
                >
                  <span
                    style={{
                      fontSize: sliderThemeStyle.current.fontSize,
                      fontWeight: sliderThemeStyle.current.fontWeight,
                      "white-space": "break-spaces",
                    }}
                  >
                    {props?.data?.style?.["Label"] ||
                    props?.data?.style?.["Label"] === ""
                      ? props?.data?.style?.["Label"]
                      : props?.data?.id}{" "}
                    {props.data.style["DefaultUnit"] &&
                      sliderThemeStyle.current.rupee !== "" &&
                      props.data.style["ManualInput"] !== "true" &&
                      "(" + sliderThemeStyle.current.rupee + ")"}
                  </span>
                  {mandatoryElem}
                </Form.Label>

                {props.data.style &&
                props.data.style["ManualInput"] === "true" ? (
                  <Form.Label style={inputStyleInputPart}>
                    <Grid
                      classes={{
                        root: classes.sliderRoot,
                      }}
                    >
                      <span
                        className="prefixSlider"
                        style={{ fontSize: sliderThemeStyle.current.fontSize }}
                      >
                        {sliderThemeStyle.current.rupee}
                      </span>
                      <input
                        aria-label={"Slide Input"}
                        type="number"
                        name="sliderInput"
                        max={maximumValueOfSlider}
                        min={minimumValueOfSlider}
                        className={`${classes.sliderInput2} sliderInput`}
                        value={value}
                        control_id={props.data.id}
                        id={userDefinedControlId + "_input"}
                        onChange={handleInputChange}
                        disabled={
                          (props.data.style["Enable"] === "true" ||
                            props.data.style["Enable"] === true) &&
                          (props.data.style["ReadOnly"] === "false" ||
                            props.data.style["ReadOnly"] === false) &&
                          (props.data.style["ReadOnlyStyle"] === "false" ||
                            props.data.style["ReadOnlyStyle"] === false)
                            ? false
                            : true
                        }
                        onBlur={handleBlur}
                        style={{
                          height: sliderThemeStyle.current.fontSize,
                          fontSize: sliderThemeStyle.current.fontSize,
                          backgroundColor: "transparent",
                        }}
                      />
                    </Grid>
                  </Form.Label>
                ) : null}
              </div>

              <Grid
                container
                spacing={2}
                alignItems="center"
                style={{ width: "100%", margin: "0px 5px" }}
              >
                <ThemeProvider theme={theme}>
                  <Slider
                    k="64"
                    step={breakpoint}
                    className={styles.slider}
                    min={minimumValueOfSlider}
                    max={maximumValueOfSlider}
                    classes={{
                      root: classes.sliderRoot,
                    }}
                    marks={marks}
                    ref={elementRefrence}
                    id={userDefinedControlId}
                    valueLabelDisplay="auto"
                    value={typeof value === "number" ? value : 0}
                    showMandatoryMsg={showMandatoryMsg}
                    onChange={handleSliderChange}
                    dataClass={
                      props.data &&
                      props.data.dataclass &&
                      props.data.dataclass != null &&
                      props.data.dataclass != undefined
                        ? props.data.dataclass
                        : ""
                    }
                    onChangeCommitted={handleBlur}
                    disabled={
                      (props.data.style["Enable"] === "true" ||
                        props.data.style["Enable"] === true) &&
                      (props.data.style["ReadOnly"] === "false" ||
                        props.data.style["ReadOnly"] === false) &&
                      (props.data.style["ReadOnlyStyle"] === "false" ||
                        props.data.style["ReadOnlyStyle"] === false)
                        ? false
                        : true
                    }
                  />
                </ThemeProvider>
              </Grid>
            </Form.Group>
            {showMandatoryMsg ? (
              <MandatoryMsg
                id={props.data.id}
                mandatoryMsg={props.data?.style?.ValidationMessage}
              />
            ) : null}
          </>
        ) : null}
        {alertData.open === true && <CustomizedAlert alertData={alertData} />}
      </>
    );
  } else {
    return (
      <>
        <Form.Group
          onClick={
            isSubformInPage
              ? () => {}
              : (e) => props.handleSelected(e, props.id, "n")
          }
          onKeyDown={(e) => props.handleSelected(e)}
          tabIndex="1"
          className={styles2.formStyle + " spacingThemeControls "}
          style={inputStylePadding}
          key={data.id}
          id={userDefinedControlId + "_group"}
          control_id={props.id}
          elem_type="slider"
          frameindex={data.frameIndex}
          elementindex={data.elementIndex}
        >
          <div
            style={{
              display: "flex",
              alignItems: "end",
              gap: "5%",
              justifyContent:
                sliderThemeStyle?.current?.float === "Left"
                  ? "flex-start"
                  : "flex-end",
            }}
          >
            <EditableLabel
              defaultLabel={
                <Form.Label
                  id={userDefinedControlId + "_label"}
                  onDoubleClick={bringEditableLabel}
                  control_id={props.id}
                  className={"controlFontSize"}
                  frameindex={props.data.frameIndex}
                  style={inputStyle}
                  elementindex={props.data.elementIndex}
                >
                  <span
                    style={{
                      fontSize: sliderThemeStyle.current.fontSize,
                      fontWeight: sliderThemeStyle.current.fontWeight,
                    }}
                  >
                    {props?.data?.style?.["Label"] ||
                    props?.data?.style?.["Label"] === ""
                      ? props?.data?.style?.["Label"]
                      : props?.data?.id}{" "}
                    {props.data.style["DefaultUnit"] &&
                      props.data.style["ManualInput"] !== "true" &&
                      sliderThemeStyle.current.rupee !== "" &&
                      "(" + sliderThemeStyle.current.rupee + ")"}
                  </span>
                  {mandatoryElem}
                </Form.Label>
              }
              editlabel={editLabel}
              default_value={defaultLabel}
              setLabelHandler={(recievedLabel) =>
                setLabelHandler(recievedLabel)
              }
            />

            {props.data.style && props.data.style["ManualInput"] === "true" ? (
              <Form.Label
                style={inputStyleInputPart}
                control_id={props.data.id}
                className="InputLable"
              >
                <Grid
                  classes={{
                    root: classes.sliderRoot,
                  }}
                  control_id={props.data.id}
                  id="sliderId"
                >
                  <span
                    className="prefixSlider"
                    style={{ fontSize: sliderThemeStyle.current.fontSize }}
                  >
                    {sliderThemeStyle.current.rupee}
                  </span>
                  <input
                    aria-label={"Slide Input2"}
                    type="number"
                    name="sliderInput"
                    max={maximumValueOfSlider}
                    min={minimumValueOfSlider}
                    className={`${classes.sliderInput2} sliderInput`}
                    value={value}
                    control_id={props.data.id}
                    id={userDefinedControlId + "_input"}
                    onChange={handleInputChange}
                    onBlur={handleBlur}
                    style={{
                      height: "calc(sliderThemeStyle.current.fontSize + 3px)",
                      fontSize: sliderThemeStyle.current.fontSize,
                      backgroundColor: "transparent",
                    }}
                  />
                  </Grid>
              </Form.Label>
            ) : null}
          </div>

          <Grid
            container
            control_id={props.data.id}
            spacing={2}
            alignItems="center"
            style={{ width: "100%", margin: "0px 5px", padding: "0px 10px" }}
          >
            <ThemeProvider theme={theme}>
              <Slider
                control_id={props.data.id}
                id={userDefinedControlId}
                step={breakpoint}
                className={styles.slider}
                min={minimumValueOfSlider}
                max={maximumValueOfSlider}
                classes={{
                  root: classes.sliderRoot,
                }}
                marks={marks}
                valueLabelDisplay="auto"
                value={typeof value === "number" ? value : 0}
                onChange={handleSliderChange}
                onBlur={handleBlur}
              />
            </ThemeProvider>
          </Grid>
          {controlSelected?.id === props?.id && (
            <ElemDeleteControl
              isSubformInPage={isSubformInPage}
              containerId={props.containerId}
              id={props.data.id}
              data={props.data}
              deleteControl={deleteControl}
              elemType={"slider"}
              setDragging={props.setDragging}
              handleReorderStart={props.handleReorderStart}
              handleReorderDrop={props.handleReorderDrop}
              dragId={props.dragId}
              dragType={"column"}
            />
          )}
        </Form.Group>
        {window?.openQuickTools && open && (
          <QuickTools
            containerId={props.containerId}
            selectedControl={{ ...props.data, ControlType: "slider" }}
            open={open}
            setOpen={setOpen}
          />
        )}
        {alertData.open === true && <CustomizedAlert alertData={alertData} />}
      </>
    );
  }
}

export default SLIDER;
